.header {
    // position: absolute
    // top: 0
    // left: 0
    // width: 100%
    padding: 20px 20px 45px;
    font-family: $tahoma;
    background-color: $white;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: flex-end; }
    &__actions {
        text-align: center; }
    &__btn {
        margin-top: 10px; } }

.main-nav {
    &__list {
        display: flex; }
    &__item {
        margin: 0 5px; }
    &__link {
        display: block;
        padding: 0 11px;
        height: 32px;
        font-size: 16px;
        color: rgba(30,30,30,.8);
        text-transform: uppercase;
        line-height: 2;
        z-index: 2; } }

.logo {
    position: relative;
    &__text {
        position: absolute;
        top: 36px;
        left: 37px;
        font-size: 7px;
        color: white;
        letter-spacing: .4px;
        white-space: nowrap;
        opacity: 0;
        -webkit-transition: opacity .3s;
        transition: opacity .3s; } }

.lang {
    display: inline-flex;
    &__link {
        padding: 3px 5px 2px;
        font-size: 12px;
        color: $dark_grey;
        line-height: 20px;
        text-transform: uppercase;
        &.is-active {
            color: $red; } } }

.order-link {
    display: inline-block;
    padding: 4px 10px 5px;
    background: red;
    border-radius: 3px;
    font-size: 15px;
    font-weight: 700;
    color: rgba($white, 0.8);
    text-transform: uppercase; }



