}

@font-face {
  font-family: "PFDinDisplayPro";
  font-style: normal;
  font-weight: 300;
  src: local("PFDinDisplayPro-Light"), local("PFDinDisplayPro-Light"), url("../fonts/PFDinDisplayPro-Light.woff2") format("woff2"), url("../fonts/PFDinDisplayPro-Light.woff") format("woff");
}

