.block {
  display: flex;
}

.block-title {
  position: relative;
  display: inline-block;
  margin-left: -15px;
  margin-top: 42px;
}

.block-title:after {
  position: absolute;
  top: 112%;
  left: 4px;
  width: calc(100% + 35px);
  height: 100%;
  content: '';
  border-top: 1px solid #91c84c;
  background-color: #fff;
  transform: rotate(-22.3deg);
  transform-origin: 0 0;
  z-index: 2;
}

.concept .block-title:after {
  background-color: #f7f7f7;
}

.block-title__number {
  font-size: 300px;
  font-weight: 700;
  color: #91c84c;
  line-height: 132px;
  letter-spacing: -0.08em;
  z-index: 1;
  text-shadow: -6px 0px 4px rgba(0, 0, 0, 0.1);
}

.block-title__rotated {
  position: absolute;
  text-align: right;
  right: 0;
  bottom: 0;
  text-transform: uppercase;
  transform: rotate(-90deg) translateX(20px);
  font-size: 110px;
  font-weight: 300;
  line-height: 1;
  color: #a8a9ad;
  z-index: 3;
  transform-origin: 100% 100%;
}

.block-title_rotated .block-title__rotated {
  transform: rotate(90deg) translate(100%, 100%);
}

.title {
  margin-bottom: 13px;
  font-size: 40px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: normal;
  line-height: 1;
}

.list-title {
  position: relative;
  padding-left: 60px;
  margin-bottom: 32px;
  font-size: 26px;
  line-height: 1.31;
  letter-spacing: 0.5px;
  font-weight: 300;
  text-transform: uppercase;
}

.list-title:before {
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  height: 2px;
  width: 40px;
  background-color: #91c84c;
  content: '';
}

.list {
  padding-left: 60px;
}

.list__item {
  position: relative;
  margin-bottom: 16px;
}

.list__item:before {
  position: absolute;
  top: calc(50% - 4px);
  left: -24px;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #5eb8e8;
  content: '';
}

.header {
  padding: 20px 20px 45px;
  font-family: Tahoma, Arial, sans-serif;
  background-color: #fff;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.header__actions {
  text-align: center;
}

.header__btn {
  margin-top: 10px;
}

.main-nav__list {
  display: flex;
}

.main-nav__item {
  margin: 0 5px;
}

.main-nav__link {
  display: block;
  padding: 0 11px;
  height: 32px;
  font-size: 16px;
  color: rgba(30, 30, 30, 0.8);
  text-transform: uppercase;
  line-height: 2;
  z-index: 2;
}

.logo {
  position: relative;
}

.logo__text {
  position: absolute;
  top: 36px;
  left: 37px;
  font-size: 7px;
  color: white;
  letter-spacing: .4px;
  white-space: nowrap;
  opacity: 0;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}

.lang {
  display: inline-flex;
}

.lang__link {
  padding: 3px 5px 2px;
  font-size: 12px;
  color: #7A7A7A;
  line-height: 20px;
  text-transform: uppercase;
}

.lang__link.is-active {
  color: #ed1c24;
}

.order-link {
  display: inline-block;
  padding: 4px 10px 5px;
  background: red;
  border-radius: 3px;
  font-size: 15px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
}

.hero {
  height: calc(100vh - 127px);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../img/hero.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-color: rgba(0, 0, 0, 0.3);
  text-align: center;
}

.hero__inner {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  width: 1000px;
  padding: 0 20px;
  margin: 0 auto;
}

.hero__logo {
  flex: 0 0 50%;
  min-width: 50%;
  text-align: left;
}

.hero__logo img {
  max-width: 100%;
}

.hero__right {
  flex: 0 0 50%;
  min-width: 50%;
  text-align: left;
}

.hero__slogan {
  position: relative;
  padding-left: 41px;
  margin-top: 108px;
  margin-bottom: 33px;
}

.hero__slogan:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  border-left: 5px solid #fff;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  width: 0;
}

.hero__title {
  font-size: 60px;
  font-weight: bold;
  font-style: normal;
  line-height: 1;
  letter-spacing: 3.6px;
  text-transform: uppercase;
  color: #fff;
}

.hero__subtitle {
  font-size: 24px;
  font-weight: 300;
  letter-spacing: 9.6px;
  color: rgba(255, 255, 255, 0.5);
}

.hero__btn {
  display: inline-block;
  padding: 10px 42px;
  font-size: 0;
  color: #fff;
  background: linear-gradient(to bottom, #e2242a, #d31a20);
  border-radius: 3px;
}

.hero__btn > * {
  display: inline-block;
  vertical-align: middle;
}

.hero__btn span {
  font-size: 14px;
  text-transform: uppercase;
  line-height: 2.14;
  letter-spacing: 1.4px;
  text-align: center;
}

.hero__btn .icon {
  font-size: 23px;
  margin-right: 12px;
}

.hero__down {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.mouse-down-btn {
  color: #fff;
  position: relative;
}

.mouse-down-btn .icon-mouse {
  font-size: 28px;
  margin-bottom: 10px;
}

.mouse-down-btn .icon-arrow-down {
  position: absolute;
  top: calc(100% + 5px);
  left: 50%;
  transform: translate(-50%, 0);
  animation: flow 2s alternate infinite both linear;
}

@keyframes flow {
  from {
    transform: translate(-50%, 0);
  }
  to {
    transform: translate(-50%, 5px);
  }
}

.created-list {
  display: flex;
  flex-wrap: wrap;
}

.created-list__item {
  flex: 0 0 50%;
  min-width: 50%;
  padding-left: 4px;
  position: relative;
}

.created-list__item:before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: 48px;
  width: 42px;
  background-color: #91c84c;
  z-index: -1;
  transform: skewY(-21.3deg);
}

.created-list__number {
  display: inline-block;
  margin-right: 9px;
  text-shadow: -3px 0px 7px rgba(0, 0, 0, 0.2);
  font-size: 140px;
  font-weight: bold;
  line-height: 1;
  color: #5eb8e8;
}

.created-list__feature {
  display: inline-block;
  margin-top: 60px;
  vertical-align: top;
  line-height: normal;
}

.about .list-title {
  margin: 90px 0 39px;
}

.about .title {
  margin-bottom: 4px;
}

.concept {
  background-color: #f7f7f7;
}

.concept__descr {
  max-width: 380px;
}

.functional__image-1 {
  margin-top: 52px;
}

.functional__list-title-2 {
  margin-bottom: 50px;
  margin-top: 140px;
}

.project__descr {
  max-width: 350px;
  margin-bottom: 100px;
}

.project__image {
  margin-left: -20px;
}

.project__image img {
  max-width: 100%;
}

.details {
  position: relative;
  z-index: 10;
}

.details__box {
  padding: 30px 40px;
  position: absolute;
  top: 0;
  left: 0;
  border-left: 8px solid #80b241;
  border-radius: 3px;
  box-shadow: -3px 0px 7px 0 rgba(0, 0, 0, 0.15);
  background-color: #91c84c;
}

.details__box:after {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 20px;
  background-image: repeating-linear-gradient(45deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 4px, transparent 4px, transparent 10px);
  content: '';
}

.details__circles {
  position: absolute;
  z-index: 10;
}

.details__circle-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: #91c84c;
  z-index: 2;
}

.details__circle-outer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 26px;
  width: 26px;
  border-radius: 50%;
  z-index: 1;
  background-color: rgba(145, 200, 76, 0.3);
}

.is-active .details__line {
  transform: scale(1);
}

[data-detail="1"] .details__circles {
  top: 45px;
  left: -295px;
}

[data-detail="1"] .details__line {
  position: absolute;
  top: 43px;
  left: -293px;
  right: 100%;
  width: 100%;
  height: 3px;
  transform: rotate(2deg);
  transform-origin: 0 0;
  overflow: hidden;
}

[data-detail="1"] .details__line:after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: red;
  content: '';
  transform: translateX(-105%);
  transition: transform 2s;
}

[data-detail="1"]:hover .details__line:after {
  transform: translateX(0);
}

[data-anim-from] {
  position: relative;
  opacity: 0;
}

[data-anim-from="bottom"] {
  transform: translateY(50px);
}

[data-anim-from="right"] {
  transform: translateX(500px);
}

body {
  font-family: "PFDinDisplayPro", serif;
}
