//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$tahoma: 'Tahoma';
$PFD: 'PFDinDisplayPro';

@font-face {
	@include fontdefault(normal, 400, $tahoma);
	@include fontpath('Tahoma', 'Tahoma', Tahoma); }

@font-face {
	@include fontdefault(normal, 700, $PFD);
	@include fontpath('PFDinDisplayPro-Bold', 'PFDinDisplayPro-Bold', PFDinDisplayPro-Bold); }

@font-face {
	@include fontdefault(normal, 300, $PFD);
	@include fontpath('PFDinDisplayPro-Light', 'PFDinDisplayPro-Light', PFDinDisplayPro-Light); }
